                    </div>
                </div>
                <h4 class="mt-0 text-gray-900 font-semibold text-base lg:text-lg leading-6">{{ event.name }}</h4>
                <div class="mt-0">
                    <span class="text-xs md:text-sm text-gray-500 font-medium">{{ prettifiedDate }}</span>
